/*--------------------------------------------------------------
# Normalize
--------------------------------------------------------------*/

html {
  font-family: sans-serif;
  -webkit-text-size-adjust: 100%;
  -ms-text-size-adjust: 100%;
}

body {
  margin: 0;
}

article,
aside,
details,
figcaption,
figure,
footer,
header,
main,
menu,
nav,
section,
summary {
  display: block;
}

audio,
canvas,
progress,
video {
  display: inline-block;
  vertical-align: baseline;
}

audio:not([controls]) {
  display: none;
  height: 0;
}

[hidden],
template {
  display: none;
}

a {
  background-color: transparent;
}

a:active,
a:hover {
  outline: 0;
}

abbr[title] {
  border-bottom: 1px dotted;
}

b,
strong {
  font-weight: bold;
}

dfn {
  font-style: italic;
}

h1 {
  font-size: 2em;
  margin: 0.67em 0;
}

mark {
  background: #ff0;
  color: #000;
}

small {
  font-size: 80%;
}

sub,
sup {
  font-size: 75%;
  line-height: 0;
  position: relative;
  vertical-align: baseline;
}

sup {
  top: -0.5em;
}

sub {
  bottom: -0.25em;
}

img {
  border: 0;
}

svg:not(:root) {
  overflow: hidden;
}

figure {
  margin: 1em 40px;
}

hr {
  box-sizing: content-box;
  height: 0;
}

pre {
  overflow: auto;
}

code,
kbd,
pre,
samp {
  font-family: monospace, monospace;
  font-size: 1em;
}

button,
input,
optgroup,
select,
textarea {
  color: inherit;
  font: inherit;
  margin: 0;
}

button {
  overflow: visible;
}

button,
select {
  text-transform: none;
}

button,
html input[type="button"],
input[type="reset"],
input[type="submit"] {
  -webkit-appearance: button;
  cursor: pointer;
}

button[disabled],
html input[disabled] {
  cursor: default;
}

button::-moz-focus-inner,
input::-moz-focus-inner {
  border: 0;
  padding: 0;
}

input {
  line-height: normal;
}

input[type="checkbox"],
input[type="radio"] {
  box-sizing: border-box;
  padding: 0;
}

input[type="number"]::-webkit-inner-spin-button,
input[type="number"]::-webkit-outer-spin-button {
  height: auto;
}

input[type="search"] {
  -webkit-appearance: textfield;
  box-sizing: content-box;
}

input[type="search"]::-webkit-search-cancel-button,
input[type="search"]::-webkit-search-decoration {
  -webkit-appearance: none;
}

fieldset {
  border: 1px solid #c0c0c0;
  margin: 0 2px;
  padding: 0.35em 0.625em 0.75em;
}

legend {
  border: 0;
  padding: 0;
}

textarea {
  overflow: auto;
}

optgroup {
  font-weight: bold;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

td,
th {
  padding: 0;
}

.wrap:after,
.container:after,
.row:after,
div[class*="col-"]:after,
.menu:after,
.breadcrumbs:after,
#shopping-cart-total:after,
.cart-item:after {
  content: "";
  display: table;
  clear: both;
}

.menu,
.breadcrumbs {
  margin: 0;
  padding: 0;
  list-style: none;
}

html {
  box-sizing: border-box;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

.col-xs-1 {
  width: 8.33333%;
}

.col-xs-2 {
  width: 16.66667%;
}

.col-xs-3 {
  width: 25%;
}

.col-xs-4 {
  width: 33.33333%;
}

.col-xs-5 {
  width: 41.66667%;
}

.col-xs-6 {
  width: 50%;
}

.col-xs-7 {
  width: 58.33333%;
}

.col-xs-8 {
  width: 66.66667%;
}

.col-xs-9 {
  width: 75%;
}

.col-xs-10 {
  width: 83.33333%;
}

.col-xs-11 {
  width: 91.66667%;
}

.col-xs-12 {
  width: 100%;
}

.wrap {
  margin-left: auto;
  margin-right: auto;
}

.container {
  margin-left: auto;
  margin-right: auto;
}

.row {
  margin-left: -6px;
  margin-right: -6px;
}

div[class*="col-"] {
  float: left;
  position: relative;
  min-height: 1px;
}

.wrap,
.container,
[class*="col-"] {
  padding-left: 6px;
  padding-right: 6px;
}

img {
  border: 0 none;
  display: block;
  max-width: 100%;
  height: auto;
}

img,
iframe {
  max-width: 100%;
}

.fill-wrap {
  margin-left: -6px;
  margin-right: -6px;
}

html {
  font-size: 100%;
}

body {
  font-family: "HelveticaNeue-Light", "Helvetica Neue Light", "Helvetica Neue", Helvetica, Arial, "Lucida Grande", sans-serif;
  font-weight: 300;
  font-size: 16px;
  line-height: 1.625;
  -webkit-font-smoothing: antialiased;
  color: #000;
}

h1 {
  font-size: 30px;
  font-size: 1.875rem;
  line-height: 45px;
  padding: 0.5em 0;
}

h2 {
  font-size: 26px;
  font-size: 1.625rem;
  line-height: 35px;
  padding: 0.5em 0;
}

h3 {
  font-size: 20px;
  font-size: 1.25rem;
  line-height: 29px;
  padding: 0.5em 0;
}

h4 {
  font-size: 16px;
  font-size: 1rem;
  line-height: 26px;
  padding: 0.5em 0;
}

h5 {
  font-size: 14px;
  font-size: 0.875rem;
  line-height: 25px;
  padding: 0.5em 0;
}

h6 {
  font-size: 12px;
  font-size: 0.75rem;
  line-height: 23px;
  padding: 0.5em 0;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-weight: 400;
  font-family: 'Oswald', sans-serif;
  margin: 0;
  color: #55667a;
}

p.lead {
  font-size: 1.125em;
}

p {
  padding: .5em 0;
  margin: 0;
}

body {
  background: #f5f5f5;
}

.wrap {
  background: #fff;
}

#site-header {
  background: #333;
}

.promo-img {
  width: 1440px;
  margin: 0 auto;
}

.menu li {
  float: left;
}

.menu li:first-child {
  margin-left: -10px;
}

.menu a {
  padding: 10px 10px;
  color: #fff;
  text-decoration: none;
  display: block;
}

.product-list-header {
  border-bottom: 1px solid #ddd;
  padding: 0 15px;
  margin-bottom: 25px;
}

#product-list {
  margin-bottom: 20px;
}

.product {
  margin-bottom: 20px;
}

.breadcrumbs {
  padding: 15px 0;
  float: left;
}

.breadcrumbs li {
  display: inline-block;
  float: left;
  margin: 0;
}

.breadcrumbs li:after {
  display: inline-block;
  content: '\00bb';
  margin: 0 .6em;
  color: #333333;
}

.breadcrumbs li:last-child:after {
  display: none;
}

.breadcrumbs li #product-list {
  padding-top: 40px;
}

.checkout {
  float: right;
  margin-top: 8px;
}

.checkout .fa {
  margin-right: 5px;
}

.checkout:disabled {
  opacity: 0.6;
}

.cart-item {
  border-bottom: 1px solid #ddd;
}

#product-list.cart-empty {
  width: 100%;
}

.product-name {
  font-size: 14px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.product-category {
  font-weight: bold;
  font-size: 12px;
}

.product-price {
  font-weight: bold;
  font-size: 20px;
  padding: 5px 0;
  display: inline-block;
  margin-right: 10px;
}

.product-quantity {
  font-size: 12px;
  font-style: italic;
}

.sold-out {
  color: #d64343;
}

.sold-out img {
  opacity: 0.4;
}

.sold-out .btn {
  background: #999;
}

#shopping-basket {
  display: none;
  min-height: 300px;
}

#shopping-cart-total {
  padding: 30px 0;
  text-align: right;
}

.cart-total {
  font-size: 20px;
}

.btn {
  display: inline-block;
  overflow: hidden;
  padding: 0 20px;
  font-size: 14px;
  font-weight: normal;
  line-height: 40px;
  text-align: center;
  white-space: nowrap;
  vertical-align: middle;
  cursor: pointer;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  border-radius: 3px;
  border: 0;
  text-decoration: none;
  background: #333;
  color: #fff;
}

.btn:disabled {
  cursor: not-allowed;
}

#voucher {
  border-radius: 3px;
  height: 42px;
  line-height: 21px;
  padding: 8px;
  font-size: 16px;
  margin-bottom: 20px;
  margin-right: 10px;
}

.apply-voucher {
  margin-top: -5px;
}

.voucher-input {
  margin-bottom: 20px;
}

.discount-status {
  background: #d64343;
  color: #fff;
  line-height: 30px;
  text-align: center;
  padding: 0 20px;
  margin: 0 0 20px 0;
  border-radius: 5px;
}

.voucher-valid {
  background: green;
}

.show-discount {
  display: block;
}

.add-to-cart {
  width: 100%;
}

.add-to-cart .fa {
  margin-left: 5px;
}

.cart-item {
  margin-bottom: 20px;
  padding-bottom: 10px;
}

.cart-image {
  float: left;
  margin-right: 20px;
  margin-bottom: 20px;
  width: 100px;
}

.cart-name {
  font-size: 16px;
}

.cart-price {
  margin-right: 10px;
}

.delete {
  float: right;
}

#site-footer {
  background: #333;
  color: #fff;
  text-align: center;
  padding: 30px 20px;
  font-size: 12px;
}

